.anna-card {
  position: relative;
  overflow: hidden;
  border-radius: 16px;
  background-color: #fdfffd;
  font-size: 28px;
  color: black; }
  .anna-card-horizontal {
    display: flex; }
  .anna-card-shadow {
    box-shadow: 0px 2px 20px 0px #eaecee; }
  .anna-card-cover > img,
  .anna-card-cover span,
  .anna-card-cover image,
  .anna-card-cover text,
  .anna-card-cover audio,
  .anna-card-cover video,
  .anna-card-cover scroll-view,
  .anna-card-cover map {
    display: block;
    width: 100%; }
  .anna-card-head {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
    padding: 24px 20px 0 20px; }
  .anna-card-title {
    font-size: 32px; }
  .anna-card-description {
    color: #999;
    font-size: 24px;
    font-weight: 400;
    margin-top: 12px; }
  .anna-card-extra {
    font-size: 28px; }
  .anna-card-content {
    padding: 24px 20px; }
    .anna-card-content-head {
      display: flex;
      justify-content: space-between;
      font-weight: 500;
      padding: 0 0 24px 0; }
  .anna-card-foot {
    background-color: #f7f7f7; }
    .anna-card-foot-horizontal {
      margin-top: 24px;
      background-color: transparent; }
