.anna-action-sheet {
  position: relative;
  overflow: hidden;
  font-size: 28px;
  color: rgba(0, 0, 0, 0.9);
  font-weight: 400;
  text-align: center; }
  .anna-action-sheet-container {
    margin: 0 24px 16px;
    border-radius: 32px;
    overflow: hidden; }
  .anna-action-sheet-cover {
    margin: 0;
    border-radius: 0; }
  .anna-action-sheet-menu {
    padding-bottom: 16px;
    background-color: #f9fbf9; }
  .anna-action-sheet-action {
    position: relative;
    height: 112px;
    line-height: 112px;
    background-color: #fdfffd;
    font-size: 34px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .anna-action-sheet-action:active {
      background-color: #ececec; }
    .anna-action-sheet-action:not(:last-child):before {
      box-sizing: border-box;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      content: '';
