.anna-steps-first_step {
  position: relative;
  height: 80px; }

.anna-steps-step {
  box-sizing: border-box;
  position: relative;
  padding: 0 0 70px 50px; }
  .anna-steps-step-header {
    display: flex;
    justify-content: space-between;
    transform: translateY(-50%); }
    .anna-steps-step-header-title {
      color: rgba(0, 0, 0, 0.85);
      font-size: 30px;
      font-weight: 500; }
    .anna-steps-step-header-extra {
      color: rgba(0, 0, 0, 0.45);
      font-size: 26px; }
  .anna-steps-step-description {
    color: rgba(0, 0, 0, 0.65);
    padding-top: 2px;
    line-height: 1.44444;
    font-size: 26px;
    transform: translateY(-10px); }

.anna-steps-circle {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #1890ff;
  z-index: 1;
  transform: translate(-50%, -50%); }

.anna-steps-circle_current {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 0;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #fdfffd;
  z-index: 1;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 30px #99CCFF; }
  .anna-steps-circle_current-icon {
    width: 40px;
    height: 40px;
    display: inline-block; }

.anna-steps-line {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: #1890ff;
  transform: translateX(-50%); }

.anna-steps_horizontal {
  display: flex;
  background-color: #fdfffd;
  overflow: hidden; }
  .anna-steps_horizontal-step {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .anna-steps_horizontal-step:nth-child(1) {
      align-items: flex-start; }
    .anna-steps_horizontal-step:nth-last-child(1) {
      align-items: flex-end; }
      .anna-steps_horizontal-step:nth-last-child(1) .anna-steps_horizontal-step-container-icon::after {
        background-color: #fdfffd; }
    .anna-steps_horizontal-step-container {
      position: relative;
      z-index: 0;
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center; }
      .anna-steps_horizontal-step-container-icon {
        position: relative;
        display: inline-block;
        padding: 0 20px;
        background-color: #fdfffd;
        height: 40px; }
        .anna-steps_horizontal-step-container-icon::after {
          content: '';
          position: absolute;
          z-index: -1;
          height: 2px;
          background-color: #f0f0f0;
          top: 50%;
          left: 50%;
          margin-top: -1px;
          width: 3000px; }
        .anna-steps_horizontal-step-container-icon-default {
          display: flex;
          align-items: center;
          height: 40px; }
          .anna-steps_horizontal-step-container-icon-default > view {
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.25); }
          .anna-steps_horizontal-step-container-icon-default > div {
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.25); }
      .anna-steps_horizontal-step-container-content-title {
        color: rgba(0, 0, 0, 0.85);
        font-size: 28px;
        font-weight: 500;
        margin-top: 10px; }
  .anna-steps_horizontal-step-finish .anna-steps_horizontal-step-container-icon::after {
    background-color: #1890ff; }
