    overflow: scroll;
    white-space: nowrap; }
    .anna-tabs-plain::-webkit-scrollbar {
      display: none; }
    .anna-tabs-plain-title {
      box-sizing: border-box;
      height: 72px;
      line-height: 72px;
      white-space: nowrap;
      margin-right: 50px;
      color: #000;
      text-align: center;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      cursor: pointer; }
    .anna-tabs-plain-active {
      box-sizing: border-box;
      position: absolute;
      left: 0;
      bottom: 0;
      height: 8px;
      width: 32px;
      border-radius: 8px;
      background-color: #1890ff;
      overflow: hidden;
      transition: all 0.2s cubic-bezier(0.645, 0.045, 0.355, 1); }
  .anna-tabs-plain-center {
    justify-content: center; }
    .anna-tabs-plain-center-title {
      flex: 1;
      text-align: center;
      padding: 0 10px;
      margin-right: 0; }
  .anna-tabs-card {
