      height: 56px;
      line-height: 56px;
      white-space: nowrap;
      color: #1890ff;
      text-align: center;
      border-right: 1PX solid #1890ff;
      cursor: pointer; }
      .anna-tabs-card-title:nth-last-child(1) {
        border-right: none; }
    .anna-tabs-card-active {
      font-weight: 500;
      color: #fdfffd;
      background-color: #1890ff; }
  .anna-tabs-slider {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    font-size: 26px; }
    .anna-tabs-slider-bg {
      padding: 6px;
      background-color: #EDEDED;
      border-radius: 72px; }
      .anna-tabs-slider-bg-square {
        border-radius: 16px; }
        .anna-tabs-slider-bg-square .anna-tabs-slider-active {
          border-radius: 12px; }
    .anna-tabs-slider-container {
      display: flex;
      justify-content: center;
      height: 60px;
      line-height: 60px;
      position: relative;
      z-index: 0; }
    .anna-tabs-slider-title {
      width: 140px;
