      transition: all 0.3s;
      overflow: hidden;
      white-space: nowrap;
      cursor: pointer; }
    .anna-tabs-slider-active {
      position: absolute;
      left: 0;
      background-color: #fdfffd;
      width: 50%;
      height: 100%;
      border-radius: 60px;
      z-index: -1;
      transform: translateX(0);
