.anna-image-upload {
  display: grid;
  grid-template-columns: repeat(auto-fill, 170px);
  grid-row-gap: 20px;
  row-gap: 20px;
  grid-column-gap: 20px;
  -moz-column-gap: 20px;
       column-gap: 20px; }
  .anna-image-upload-add {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 170px;
    border-radius: 10px;
    background-color: #f9fbf9;
    cursor: pointer; }
  .anna-image-upload-item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 170px;
    border-radius: 10px;
    background-color: #e8e8e8;
    overflow: hidden;
    cursor: pointer; }
    .anna-image-upload-item-delete {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 5px;
      right: 5px;
      width: 30px;
      height: 30px;
      background-color: #999;
      opacity: 0.8;
      border-radius: 50%; }
    .anna-image-upload-item > image {
      width: 100%; }
