.anna-btn {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  padding: 0 32px;
  background-color: #ededef;
  height: 64px;
  line-height: 60px;
  border: 1px solid #ededef;
  border-radius: 64px;
  color: #1890ff;
  font-weight: 500;
  font-size: 28px;
  text-align: center;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  touch-action: manipulation;
  -webkit-appearance: none;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  margin: 0;
  outline: 0;
  cursor: pointer;
  overflow: unset; }
  .anna-btn:not(.anna-btn-disabled):active .anna-btn-mask {
    box-sizing: border-box;
    position: absolute;
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;
    content: '';
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: inherit;
    pointer-events: none;
    transition: background-color 0.3s; }
  .anna-btn:after {
    display: none; }
  .anna-btn-text {
    font-weight: 500; }
  .anna-btn-small {
    font-size: 24px;
    padding: 0 24px;
    height: 48px;
    line-height: 44px;
    border-radius: 52px; }
  .anna-btn-large {
    padding: 0 40px;
    height: 80px;
    line-height: 76px;
    border-radius: 80px; }
  .anna-btn-superlarge {
    padding: 0 40px;
    height: 84px;
    line-height: 80px;
    border-radius: 84px;
    font-size: 30px; }
  .anna-btn-primary {
    border-color: #1890ff;
    background-color: #1890ff;
    color: #fdfffd; }
  .anna-btn-plain {
    font-weight: 400;
    color: #666;
    border: 1px solid #ddd;
    background-color: #fdfffd; }
  .anna-btn-plain.anna-btn-primary {
    border-color: #1890ff;
    color: #1890ff; }
  .anna-btn-hairline {
    line-height: 64px;
    border-width: 0; }
    .anna-btn-hairline:before {
      box-sizing: border-box;
      position: absolute;
      top: -50%;
      right: -50%;
      bottom: -50%;
      left: -50%;
      content: '';
      border: 1px solid #ddd;
      border-color: inherit;
      border-radius: inherit;
      transform: scale(0.5);
      pointer-events: none; }
  .anna-btn-small.anna-btn-hairline {
    line-height: 48px; }
  .anna-btn-large.anna-btn-hairline {
    line-height: 80px; }
  .anna-btn-superlarge.anna-btn-hairline {
    line-height: 84px; }
  .anna-btn-square {
    padding: 0 30px;
    border-radius: 12px; }
  .anna-btn-square.anna-btn-small {
    padding: 0 14px; }
  .anna-btn-circle {
    padding: 0;
    min-width: 64px;
    border-radius: 50%;
    z-index: 9; }
  .anna-btn-circle.anna-btn-small {
    min-width: 48px; }
  .anna-btn-circle.anna-btn-large {
    min-width: 80px; }
  .anna-btn-circle.anna-btn-superlarge {
    min-width: 84px; }
  .anna-btn-disabled {
    cursor: default;
    opacity: 0.4;
    transition: opacity 0.2s; }
  .anna-btn-danger-default {
    color: #ff4d4f; }
  .anna-btn-danger {
    border-color: #ff4d4f;
    background-color: #ff4d4f;
    color: #fdfffd; }
  .anna-btn-look-secondary {
    color: #1890ff;
    background-color: #ecf5ff;
    border-color: #ecf5ff; }
  .anna-btn-look-secure {
    color: #fdfffd;
    background-color: #07c160;
    border-color: #07c160; }
  .anna-btn-look-warning {
    color: black;
    background-color: #ffc409;
    border-color: #ffc409; }
  .anna-btn-look-light {
    color: black;
    background-color: #f4f5f8;
    border-color: #f4f5f8; }
  .anna-btn-look-medium {
    color: #fdfffd;
    background-color: #92949c;
    border-color: #92949c; }
  .anna-btn-look-dark {
    color: #fdfffd;
    background-color: #222428;
    border-color: #222428; }
  .anna-btn-look-anna {
    color: #fdfffd;
    background-color: #ff7777;
    border-color: #ff7777; }
  .anna-btn-look-orange {
    color: #fdfffd;
    background-color: #ff4f00;
    border-color: #ff4f00; }
  .anna-btn-block {
    width: 100%; }
  .anna-btn-float {
    position: fixed;
    box-shadow: 0 6px 24px #ccc; }
    .anna-btn-float-center {
      left: 50%;
      bottom: 60px;
      transform: translateX(-50%); }
    .anna-btn-float-left {
      left: 24px;
      bottom: 60px; }
    .anna-btn-float-right {
      right: 24px;
      bottom: 60px; }
  .anna-btn-loading {
    padding-left: 84px;
    transition: padding 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }
    .anna-btn-loading-icon {
      display: inline;
      padding-right: 16px;
      margin-left: -52px; }
  .anna-btn-ghost {
    border-color: transparent;
    background-color: transparent; }

.width-fix {
  height: 80px;
  display: inline-block;
  overflow: hidden; }
  .width-fix .default {
    display: inline-block;
    width: 200%;
    height: 200%;
    font-size: 56px;
    transform: scale(0.5);
    transform-origin: 0 0;
    text-align: center; }
  .width-fix .large {
    line-height: 160px;
    border-radius: 160px; }
  .width-fix .primary {
    background-color: #1890ff;
    color: #fdfffd; }
  .width-fix .disabled {
    opacity: 0.4;
    color: rgba(255, 255, 255, 0.6) !important; }
