.anna-rate {
  display: flex;
  align-items: center;
  padding-bottom: 30px; }
  .anna-rate:nth-last-child(1) {
    padding-bottom: 0; }
  .anna-rate-label {
    padding-right: 40px; }
  .anna-rate-value {
    flex: 1;
    display: flex; }
    .anna-rate-value-star {
      margin-right: 30px;
      cursor: pointer; }
      .anna-rate-value-star:nth-last-child(1) {
        margin-right: 0; }
