.anna-stepper {
  display: inline-flex;
  align-items: center;
  height: 56px;
  line-height: 56px;
  font-size: 32px; }
  .anna-stepper-small {
    height: 48px;
    line-height: 48px; }
    .anna-stepper-small > .anna-stepper-btn {
      width: 48px;
      height: 48px; }
      .anna-stepper-small > .anna-stepper-btn-plain {
        width: 44px;
        height: 44px; }
  .anna-stepper-minus {
    border-radius: 8px 0 0 8px; }
  .anna-stepper-plus {
    border-radius: 0 8px 8px 0; }
  .anna-stepper-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 56px;
    height: 56px;
    background-color: #F5F5F5;
    text-align: center;
    font-size: 36px;
    cursor: pointer; }
    .anna-stepper-btn:active {
      background-color: #ececec; }
    .anna-stepper-btn-disabled {
      cursor: not-allowed;
      background-color: #FBFBFB; }
      .anna-stepper-btn-disabled:active {
        background-color: #FBFBFB; }
    .anna-stepper-btn-plain {
      width: 52px;
      height: 52px;
      border: 2px solid #DDD;
      border-radius: 8px;
      background-color: transparent; }
      .anna-stepper-btn-plain:active {
        background-color: transparent; }
      .anna-stepper-btn-plain-disabled {
        cursor: not-allowed;
        background-color: transparent;
        opacity: 0.4; }
    .anna-stepper-btn-circle {
      border-radius: 50%; }
  .anna-stepper-input-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 80px;
    margin: 0 4px;
    background-color: #FDFFFD;
    overflow: hidden; }
  .anna-stepper-input {
    height: 100%;
    width: 100%;
    padding: 0;
    background-color: #F5F5F5;
    outline: none;
    border: none;
    color: #333333;
    font-size: 30px;
    text-align: center; }
    .anna-stepper-input-disabled {
      background-color: #FBFBFB; }
    .anna-stepper-input-plain {
      background-color: transparent; }
    .anna-stepper-input-circle {
      border-radius: 16px; }
