.anna-picker-view-toolbar {
  height: 88px;
  line-height: 88px;
  display: flex;
  justify-content: space-between; }

.anna-picker-view-cancel {
  padding: 0 24px;
  color: #999; }

.anna-picker-view-title {
  color: #333;
  font-weight: 500; }

.anna-picker-view-confirm {
  padding: 0 24px;
  color: #1890ff; }

.anna-picker-view-columns {
  position: relative;
  display: flex;
  height: 440px; }

.anna-picker-view-selected {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  top: 176px;
  width: 100%;
  height: 88px;
  background-color: rgba(102, 204, 255, 0.08);
  pointer-events: none; }

.anna-picker-view-mask {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.4)), linear-gradient(0deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.4));
  background-repeat: no-repeat;
  background-position: top, bottom;
  background-size: 100% 176px;
  pointer-events: none; }
