.anna-skeleton {
  position: relative; }
  .anna-skeleton-placeholder {
    visibility: visible;
    opacity: 1;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }
  .anna-skeleton-item {
    margin-bottom: 60px; }
    .anna-skeleton-item:nth-last-child(1) {
      margin-bottom: 0; }
  .anna-skeleton-hidden {
    visibility: hidden;
    opacity: 0; }
  .anna-skeleton-title {
    margin-left: 24px;
    height: 26px;
    border-radius: 6px;
    width: 260px;
    background-color: rgba(24, 144, 255, 0.2);
    margin-bottom: 16px; }
  .anna-skeleton-row {
    margin: 0 24px;
    height: 20px;
    border-radius: 6px;
    background-color: #ededef;
    margin-bottom: 16px; }
  .anna-skeleton-image {
    margin: 24px;
    height: 340px;
    border-radius: 16px;
    background-color: #ededef; }
  .anna-skeleton-avatar {
    margin: 0 24px 16px;
    width: 74px;
    height: 74px;
    border-radius: 50%;
    background-color: #ededef; }
  .anna-skeleton-container {
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }
  .anna-skeleton-container-active {
    visibility: visible;
    opacity: 1; }
  .anna-skeleton-active {
    background: linear-gradient(90deg, #ededef 25%, #e6e6e6 37%, #ededef 63%);
    background-size: 400% 100%;
    -webkit-animation: anna-skeleton-loading 1.4s ease infinite;
            animation: anna-skeleton-loading 1.4s ease infinite; }

@-webkit-keyframes anna-skeleton-loading {
  0% {
    background-position: 100% 50%; }
  100% {
    background-position: 0 50%; } }

@keyframes anna-skeleton-loading {
  0% {
    background-position: 100% 50%; }
  100% {
    background-position: 0 50%; } }
