.anna-swipe {
  position: relative;
  overflow: hidden; }
  .anna-swipe-container {
    position: relative;
    font-size: 30px;
    font-weight: 400;
    transition: all 0.3s; }
  .anna-swipe-actions {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 100%;
    background-color: #fdfffd;
    color: #fdfffd;
    font-size: 32px;
    font-weight: 500;
    display: flex; }
  .anna-swipe-action {
    padding: 0 34px;
    background-color: #ff4d4f;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    cursor: pointer; }
