.anna-filter {
  position: relative;
  z-index: 999;
  height: 100px; }
  .anna-filter-items {
    position: relative;
    display: flex; }
  .anna-filter-content {
    z-index: 1;
    position: fixed;
    top: unset;
    bottom: unset;
    height: 100%;
    width: 100%;
    overflow: hidden;
    transition: height 0.3s ease; }

.anna-filter-item {
  overflow: hidden;
  flex: 1; }
  .anna-filter-item-label {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    font-size: 24px;
    color: #333;
    background-color: #fdfffd;
    cursor: pointer; }
    .anna-filter-item-label-disabled {
      opacity: 0.4; }
    .anna-filter-item-label-text {
      margin-right: 10px; }
    .anna-filter-item-label-chevronUp {
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-bottom: 10px solid #CCCCCC; }
    .anna-filter-item-label-chevronDown {
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 10px solid #CCCCCC; }
  .anna-filter-item-value {
    background-color: #fdfffd;
    max-height: 80vh;
    z-index: -1; }
