.anna-cascade {
  background-color: #fafafa;
  color: #333;
  font-size: 30px;
  text-align: left; }
  .anna-cascade-selected {
    min-height: 90px;
    background-color: #fdfffd;
    padding: 24px 0; }
  .anna-cascade-step {
    display: flex;
    padding-bottom: 30px;
    cursor: pointer; }
    .anna-cascade-step:nth-last-child(1) {
      padding-bottom: 0; }
    .anna-cascade-step-dot {
      position: absolute;
      width: 16px;
      height: 16px;
      top: 30px;
      left: 100%;
      margin-top: -8px;
      margin-left: -8px;
      border-radius: 50%;
      background-color: #1890FF;
      z-index: 10; }
    .anna-cascade-step-line {
      position: relative;
      width: 30px;
      min-width: 30px;
      margin-right: 30px; }
      .anna-cascade-step-line-default::before {
        content: '';
        position: absolute;
        width: 2px;
        height: calc(100% + 30px);
        background-color: #E8E8E8;
        top: 30px;
        left: 100%;
        margin-left: -1px; }
      .anna-cascade-step-line-active::before {
        content: '';
        position: absolute;
        width: 2px;
        height: calc(100% + 30px);
        background-color: #1890ff;
        top: 30px;
        left: 100%;
        margin-left: -1px; }
    .anna-cascade-step-container {
      flex-grow: 1; }
    .anna-cascade-step-content {
      display: flex;
      justify-content: space-between;
      align-items: center; }
    .anna-cascade-step-left {
      display: flex;
      align-items: center;
      height: 60px; }
    .anna-cascade-step-right {
      display: flex;
      align-items: center;
      padding-right: 20px;
      color: #999; }
      .anna-cascade-step-right-text {
        padding-right: 10px; }
      .anna-cascade-step-right-arrow {
        min-width: 24px; }
  .anna-cascade-showed {
    min-height: 200px;
    padding: 0 30px; }
    .anna-cascade-showed-title {
      padding: 30px 0 10px 0;
      font-size: 24px;
      color: #666; }
    .anna-cascade-showed-category {
      padding: 30px 0;
      cursor: pointer; }
