.anna-input {
  display: inline-block;
  width: 100%;
  height: 44px;
  line-height: 44px;
  text-align: left;
  color: #333333;
  text-overflow: ellipsis;
  padding: 0;
  outline: none;
  border: none; }
  .anna-input-wrapper {
    display: inline-flex;
    align-items: center;
    position: relative;
    width: 100%;
    background-color: #fdfffd;
    border-radius: 4px;
    border: 1px solid #d9d9d9;
    overflow: hidden;
    padding: 8px 20px;
    font-size: 28px; }
    .anna-input-wrapper-small {
      padding: 0 12px; }
    .anna-input-wrapper-large {
      padding: 12px 20px; }
    .anna-input-wrapper-small > .anna-input {
      font-size: 24px; }
    .anna-input-wrapper-large > .anna-input {
      height: 50px;
      line-height: 50px;
      font-size: 32px; }
  .anna-input-prefix {
    margin-right: 8px; }
  .anna-input-suffix {
    margin-left: 8px; }
  .anna-input-align-right {
    text-align: right; }
  .anna-input-align-center {
    text-align: center; }
  .anna-input-container {
    display: flex; }
  .anna-input-extra {
    margin-left: 8px; }
