.anna-ling {
  position: fixed;
  z-index: 1010;
  background-color: #fefffe;
  overflow: hidden;
  box-shadow: 0px 2px 20px 0px #eaecee;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }
  .anna-ling-top {
    width: 400px;
    height: 100px;
    border-radius: 100px;
    left: 50%;
    top: 0;
    transform: translate3d(-50%, calc(-100% - 10px), 0); }
  .anna-ling-show {
    transform: translate3d(-50%, 24px, 0); }
  .anna-ling-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    height: 100%;
    font-weight: 500; }
  .anna-ling-title {
    color: #666; }
  .anna-ling-content {
    color: #999;
    margin-top: 10px; }
  .anna-ling-icon {
    position: absolute;
    left: 24px;
    top: 50%;
    transform: translateY(-50%); }
