.anna-spin {
  display: flex;
  justify-content: center;
  align-items: center; }
  .anna-spin-loading {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 2px solid #999;
    border-bottom-color: transparent;
    -webkit-animation: loading .5s linear infinite;
            animation: loading .5s linear infinite;
    margin: 0 auto; }
  .anna-spin-default {
    width: 50px;
    height: 50px; }

@-webkit-keyframes loading {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }

@keyframes loading {
  from {
    transform: rotate(0); }
  to {
    transform: rotate(360deg); } }
