.anna-selector {
  display: flex;
  text-align: left; }
  .anna-selector-parent {
    width: 280px;
    background-color: #f7f7f7; }
  .anna-selector-children {
    flex: 1; }
  .anna-selector-option {
    position: relative;
    box-sizing: border-box;
    padding-left: 30px;
    height: 88px;
    line-height: 88px;
    font-size: 28px;
    color: #666;
    cursor: pointer; }
    .anna-selector-option-rounded_bottom {
      position: absolute;
      bottom: -12px;
      right: 0;
      width: 12px;
      height: 12px;
      background-color: #fdfffd;
      overflow: hidden; }
      .anna-selector-option-rounded_bottom-circle {
        position: absolute;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        left: -12px;
        right: -12px;
        background-color: #f7f7f7; }
    .anna-selector-option-rounded_top {
      position: absolute;
      top: -12px;
      right: 0;
      width: 12px;
      height: 12px;
      background-color: #fdfffd;
      overflow: hidden;
      transform: rotate(90deg); }
      .anna-selector-option-rounded_top-circle {
        position: absolute;
        width: 24px;
        height: 24px;
        border-radius: 50%;
        left: -12px;
        right: -12px;
        background-color: #f7f7f7; }
  .anna-selector-active {
    color: #1890ff;
    font-weight: 500;
    background-color: #fdfffd; }
