.anna-cell {
  position: relative;
  padding: 20px 32px;
  font-size: 28px;
  line-height: 48px;
  background-color: #fdfffd;
  margin-bottom: 1px; }
  .anna-cell-verticality-required {
    color: #f5222d;
    margin-right: 4px; }
  .anna-cell-verticality-label {
    color: #333333;
    padding-bottom: 20px; }
  .anna-cell-verticality-value {
    color: #999999; }
  .anna-cell-container-main {
    display: flex; }
    .anna-cell-container-main-left {
      box-sizing: border-box;
      flex: 1;
      display: flex;
      align-items: center; }
      .anna-cell-container-main-left-input {
        flex: none; }
      .anna-cell-container-main-left-label {
        box-sizing: border-box;
        color: #333333;
        font-weight: 500;
        flex: 1;
        display: flex;
        align-items: center; }
        .anna-cell-container-main-left-label-required {
          color: #f5222d;
          margin-right: 4px; }
        .anna-cell-container-main-left-label-value {
          margin-right: 20px; }
    .anna-cell-container-main-right {
      flex: 1;
      color: #999999;
      display: flex;
      align-items: center;
      justify-content: flex-end; }
      .anna-cell-container-main-right-value {
        width: 100%;
        text-align: right; }
  .anna-cell-container-description {
    margin-top: 10px;
    font-size: 24px;
    color: #999999; }
  .anna-cell-border {
    position: absolute;
    left: 32px;
    right: 0;
    bottom: -1px;
    height: 1px;
    background-color: #e8e8e8;
    transform: scaleY(0.5); }
  .anna-cell-input {
    height: 48px;
    line-height: 48px;
    min-height: 48px;
    border: none; }
