.anna-swiper {
  position: relative;
  overflow: hidden;
  border-radius: 16px;
  height: 240px; }
  .anna-swiper-square {
    border-radius: 16px; }
  .anna-swiper-sharp {
    border-radius: 0; }
  .anna-swiper-round {
    border-radius: 240px; }
  .anna-swiper-mini {
    height: 76px; }
  .anna-swiper-small {
    height: 200px; }
  .anna-swiper-middle {
    height: 240px; }
  .anna-swiper-large {
    height: 360px; }
  .anna-swiper-superlarge {
    height: 750px; }
  .anna-swiper-track {
    display: flex;
    align-items: center;
    cursor: pointer;
    height: 100%; }
    .anna-swiper-track-vertical {
      flex-direction: column; }
  .anna-swiper-item {
    flex-shrink: 0;
    height: 100%;
    width: 100%; }
  .anna-swiper-indicators {
    position: absolute;
    display: flex; }
    .anna-swiper-indicators-bottom {
      bottom: 12px;
      left: 50%;
      transform: translateX(-50%); }
    .anna-swiper-indicators-bottom-left {
      bottom: 16px;
      left: 30px; }
    .anna-swiper-indicators-bottom-right {
      bottom: 16px;
      right: 30px; }
    .anna-swiper-indicators-left {
      flex-direction: column;
      left: 12px;
      top: 50%;
      transform: translateY(-50%); }
    .anna-swiper-indicators-right {
      flex-direction: column;
      right: 12px;
      top: 50%;
      transform: translateY(-50%); }
    .anna-swiper-indicators-outside {
      bottom: -30px;
      left: 50%;
      transform: translateX(-50%); }
  .anna-swiper-indicator {
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.5); }
    .anna-swiper-indicator-active {
      background-color: #feffff; }
    .anna-swiper-indicator-bottom {
      margin-right: 12px; }
    .anna-swiper-indicator-bottom-left {
      margin-right: 12px; }
    .anna-swiper-indicator-bottom-right {
      margin-right: 12px; }
    .anna-swiper-indicator-left {
      margin-bottom: 12px; }
    .anna-swiper-indicator-right {
      margin-bottom: 12px; }
    .anna-swiper-indicator:nth-last-child(1) {
      margin-right: 0;
      margin-bottom: 0; }
