.anna-result {
  box-sizing: border-box;
  height: 100vh;
  padding: 100px 30px;
  background-color: #fdfffd; }
  .anna-result-icon {
    margin-bottom: 48px;
    text-align: center; }
    .anna-result-icon-status {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 144px;
      height: 144px;
      border-radius: 50%; }
  .anna-result-title {
    color: #333;
    font-size: 48px;
    line-height: 1.8;
    text-align: center; }
  .anna-result-sub_title {
    color: #999;
    font-size: 28px;
    line-height: 1.6;
    text-align: center; }
  .anna-result-content {
    margin-top: 48px;
    padding: 24px 40px;
    background-color: #fafafa; }
  .anna-result-extra {
    margin-top: 64px;
    text-align: center; }
