.anna-checkbox {
  display: inline-block;
  margin-right: 30px;
  font-size: 28px; }
  .anna-checkbox:nth-last-child(1) {
    margin-right: 0; }
  .anna-checkbox-container {
    display: flex;
    align-items: center;
    cursor: pointer; }
    .anna-checkbox-container-icon {
      font-size: 48px;
      width: 48px;
      height: 48px; }
    .anna-checkbox-container-label {
      margin-left: 14px;
      color: #333;
      white-space: nowrap; }
