.anna-progress-outer {
  display: inline-block;
  width: 100%;
  padding-right: 0;
  margin-right: 0; }
  .anna-progress-show-info .anna-progress-outer {
    padding-right: calc(56px + 16px);
    margin-right: calc(-56px - 16px); }

.anna-progress-inner {
  position: relative;
  display: inline-block;
  width: 100%;
  border-radius: 200px;
  background-color: #f5f5f5;
  overflow: hidden;
  vertical-align: middle; }

.anna-progress-success-bg, .anna-progress-bg {
  position: relative;
  border-radius: 200px;
  background-color: #1890ff;
  transition: all 0.4s cubic-bezier(0.08, 0.82, 0.17, 1) 0s; }

.anna-progress-success-bg {
  position: absolute;
  left: 0;
  top: 0;
  background-color: #52c41a; }
