.anna-radio {
  display: inline-block;
  margin-right: 30px;
  font-size: 28px; }
  .anna-radio:nth-last-child(1) {
    margin-right: 0; }
  .anna-radio-container {
    display: flex;
    align-items: center;
    cursor: pointer; }
  .anna-radio-out-round {
    position: relative;
    width: 38px;
    height: 38px;
    padding: 7px;
    border-radius: 50%; }
    .anna-radio-out-round::before {
      box-sizing: border-box;
      position: absolute;
      content: '';
      left: -50%;
      right: -50%;
      top: -50%;
      bottom: -50%;
      transform: scale(0.5);
      border-radius: 50%;
      border: 1PX solid #999;
      pointer-events: none; }
    .anna-radio-out-round-checked::before {
      border: 2PX solid #1890ff; }
  .anna-radio-inner-round {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #1890ff; }
  .anna-radio-children {
    margin-left: 14px;
    color: #333;
    white-space: nowrap; }
