.anna-tag {
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  text-align: center;
  color: #333;
  background-color: #f6f6f6;
  border: 2px solid #f6f6f6;
  height: 44px;
  line-height: 40px;
  padding: 0 14px;
  border-radius: 8px;
  font-size: 24px;
  margin-right: 14px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  white-space: nowrap; }
  .anna-tag:nth-last-child(1) {
    margin-right: 0; }
  .anna-tag-plain {
    background-color: transparent;
    border-color: transparent; }
    .anna-tag-plain::before {
      box-sizing: border-box;
      position: absolute;
      content: '';
      left: -50%;
      right: -50%;
      top: -50%;
      bottom: -50%;
      border-radius: 16px;
      border: 1PX solid #CCCCCC;
      transform: scale(0.5); }
  .anna-tag-small {
    height: 40px;
    line-height: 36px;
    padding: 0 12px;
    border-radius: 6px;
    font-size: 22px; }
  .anna-tag-large {
    height: 52px;
    line-height: 48px;
    padding: 0 20px;
    border-radius: 8px;
    font-size: 24px; }
  .anna-tag-block {
    width: 100%; }
  .anna-tag-checkable {
    cursor: pointer;
    background-color: #F5F5F5; }
  .anna-tag-checked {
    color: #fdfffd;
    background-color: #1890ff;
    border: 2px solid #1890ff; }
  .anna-tag-blue {
    color: #1890ff;
    background-color: #e6f7ff;
    border: 2px solid #91d5ff; }
  .anna-tag-green {
    color: #52c41a;
    background-color: #f6ffed;
    border: 2px solid #b7eb8f; }
  .anna-tag-yellow {
    color: #fa8c16;
    background-color: #fff7e6;
    border: 2px solid #ffd591; }
  .anna-tag-red {
    color: #f5222d;
    background-color: #fff1f0;
    border: 2px solid #ffa39e; }
  .anna-tag-disabled {
    opacity: 0.4; }
